import React, { useState, useEffect, useCallback, useRef } from 'react';
import { useLocation, Link } from 'react-router-dom';
import axios from 'axios'; 
import './Topo.css';
import { useNotificacoes } from '../context/NotificacaoContext'; 
import { formatDistanceToNow } from 'date-fns'; 
import ptBR from 'date-fns/locale/pt-BR'; 

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

// Mapeamento dos ttulos das pginas (ajuste conforme suas rotas)
const pageTitles = {
    '/inicio': 'Incio',
    '/pacientes': 'Pacientes',
    '/patients/new': 'Novo Paciente', // Ajustado para corresponder  rota
    '/patients/:id': 'Detalhes do Paciente', // Mantm o padro genrico
    '/patients/:id/edit': 'Editar Paciente', // Adicionada rota de edio
    '/agenda': 'Agenda',
    '/orcamentos': 'Oramentos',
    '/pagamentos': 'Pagamentos',
    '/notificacoes': 'Notificaes',
};

function Header() {
    const location = useLocation();
    // Pega dados e funes do Contexto de Notificaes
    const { unreadCount, fetchUnreadCount, isLoadingCount } = useNotificacoes();

    // Estados locais para o dropdown
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);
    const [recentNotifications, setRecentNotifications] = useState([]);
    const [isLoadingDropdown, setIsLoadingDropdown] = useState(false);

    // Ref para o container do dropdown (para detectar cliques fora)
    const dropdownRef = useRef(null);

    // Funo para buscar as notificaes recentes NO LIDAS
    const fetchRecentNotifications = useCallback(async () => {
        // No busca se no houver no lidas ou se j estiver buscando
        if (unreadCount === 0 || isLoadingDropdown) {
            setRecentNotifications([]);
            return;
        }
        setIsLoadingDropdown(true);
        const token = localStorage.getItem('access_token');
        if (!token) { setIsLoadingDropdown(false); return; }

        try {
            // Busca as X mais recentes no lidas (ex: limit=5)
            const response = await axios.get(`${API_BASE_URL}/api/notificacoes/?lida=false&limit=5`, {
                headers: { 'Authorization': `Bearer ${token}` }
            });
            const notificationsData = response.data.results || (Array.isArray(response.data) ? response.data : []);
            setRecentNotifications(notificationsData);
        } catch (error) {
            console.error("Erro ao buscar notificaes recentes:", error);
            setRecentNotifications([]); // Limpa em caso de erro
             // Tratar erro 401 (opcional aqui, j tratado no context)
        } finally {
            setIsLoadingDropdown(false);
        }
    }, [unreadCount, isLoadingDropdown]); // Adicionado isLoadingDropdown como dependncia

    // Funo para abrir/fechar o dropdown
    const toggleDropdown = () => {
        const nextState = !isDropdownOpen;
        setIsDropdownOpen(nextState);
        // Busca notificaes ao abrir (se houver no lidas)
        if (nextState && unreadCount > 0) {
            fetchRecentNotifications();
        }
    };

    // Funo para marcar todas como lidas
    const handleMarkAllRead = async () => {
        
        // Verifica se h algo para marcar
        if (unreadCount === 0) {
            setIsDropdownOpen(false); // Fecha o dropdown se j estiver vazio
            return;
        }

        try {
            const token = localStorage.getItem('access_token');
            if (!token) throw new Error("Token no encontrado");

            // Chama o novo endpoint do backend usando POST
            await axios.post(
                `${API_BASE_URL}/api/notificacoes/marcar-todas-como-lidas/`, 
                {}, // Corpo da requisio vazio, pois a ao no precisa de dados extras
                { headers: { 'Authorization': `Bearer ${token}` } }
            ); 
            
            // Aps sucesso:
            fetchUnreadCount(); // Pede ao Contexto para atualizar a contagem global (deve ir para 0)
            setRecentNotifications([]); // Limpa a lista local no dropdown
            setIsDropdownOpen(false); // Fecha o dropdown

        } catch (error) {
            console.error("Erro ao marcar todas como lidas:", error.response?.data || error.message);
            alert("Erro ao marcar notificaes como lidas. Verifique o console.");
            // No fecha o dropdown em caso de erro para o usurio ver
        }
    };

     // Efeito para fechar dropdown ao clicar fora
     useEffect(() => {
         const handleClickOutside = (event) => {
             // Verifica se o clique foi fora do elemento referenciado pelo dropdownRef
             if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                 setIsDropdownOpen(false);
             }
         };
         if (isDropdownOpen) {
             document.addEventListener('mousedown', handleClickOutside);
         } else {
             document.removeEventListener('mousedown', handleClickOutside);
         }
         return () => { // Limpeza ao desmontar
             document.removeEventListener('mousedown', handleClickOutside);
         };
     }, [isDropdownOpen]);


    // Funo para formatar tempo relativo
    const formatTempoAtras = (dateString) => {
         try {
             const date = new Date(dateString);
             return formatDistanceToNow(date, { addSuffix: true, locale: ptBR });
         } catch (e) {
             console.error("Erro formatando data:", dateString, e);
             return dateString;
         }
    };

    // Funo para pegar o ttulo da pgina (ajustada para rotas dinmicas)
    const getPageTitle = () => {
        let title = 'DrGus'; // Ttulo padro
        // Verifica rotas exatas primeiro
        if (pageTitles[location.pathname]) {
            title = pageTitles[location.pathname];
        } else {
            // Verifica rotas com parmetros (ex: /patients/:id)
            for (const path in pageTitles) {
                if (path.includes(':')) {
                    // Cria uma regex a partir do padro da rota
                    // Ex: /patients/:id vira /patients/[^/]+
                    const regexPath = new RegExp(`^${path.replace(/:\w+/g, '[^/]+')}$`);
                    if (regexPath.test(location.pathname)) {
                        title = pageTitles[path];
                        break; // Encontrou, pode parar
                    }
                }
            }
        }
        return title;
    };
    const currentPageTitle = getPageTitle();

    return (
        <header className="app-header-common">
            <div className="page-title">{currentPageTitle}</div>

            {/* cone de Notificao agora como boto com dropdown */}
            <div className="notification-icon-wrapper" ref={dropdownRef}>
                <button type="button" className="notification-icon-button" onClick={toggleDropdown} aria-label="Abrir notificaes" aria-haspopup="true" aria-expanded={isDropdownOpen}>
                    <i className='bx bxs-bell'></i> {/* cone do sino */}
                     {/* Badge (mostra se no estiver carregando e houver > 0) */}
                     {!isLoadingCount && unreadCount > 0 && (
                         <span className="notification-badge">{unreadCount > 9 ? '9+' : unreadCount}</span>
                     )}
                </button>

                {/* O Dropdown */}
                {isDropdownOpen && (
                    <div className="notification-dropdown">
                        <div className="dropdown-header">
                            <span>Notificaes Recentes</span>
                            {/* Mostra boto "Marcar todas" apenas se houver notificaes listadas */}
                            {!isLoadingDropdown && recentNotifications.length > 0 && (
                                <button onClick={handleMarkAllRead} className="mark-all-read-button">
                                    Marcar todas como lidas
                                </button>
                            )}
                        </div>
                        <ul className="notification-list">
                            {isLoadingDropdown ? (
                                <li className="notification-item loading">Carregando...</li>
                            ) : recentNotifications.length > 0 ? (
                                recentNotifications.map(notif => (
                                    <li key={notif.id_notificacao} className="notification-item">
                                            <div className="notification-link"> {/* Div para manter estilo */}
                                                <p className="notification-message">{notif.mensagem}</p>
                                                <span className="notification-time">{formatTempoAtras(notif.data_criacao)}</span>
                                            </div>
                                    </li>
                                ))
                            ) : (
                                <li className="notification-item empty">Nenhuma notificao nova.</li>
                            )}
                        </ul>
                         <div className="dropdown-footer">
                             {/* Link para a pgina principal de notificaes */}
                             <Link to="/notificacoes" onClick={() => setIsDropdownOpen(false)}>
                                 Ver todas as notificaes
                             </Link>
                         </div>
                    </div>
                )}
            </div>
        </header>
    );
}

export default Header;