import React, { useState } from 'react';
import './AjudaGeral.css';
import Modal from '../componentes/Modal';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faQuestionCircle } from '@fortawesome/free-solid-svg-icons';

function AjudaGeral() {
  const [isAjudaOpen, setIsAjudaOpen] = useState(false);

  return (
    <div className="ajuda-container">

      <h2>Ajuda Geral - Fluxo do Sistema</h2>

      <div className="ajuda-section">
        <h3>Viso Geral</h3>
        <p>
          Bem-vindo  ajuda do DrGus. Este sistema foi projetado para gerenciar o fluxo completo de um consultrio odontolgico,
          desde o cadastro do paciente at a finalizao de um tratamento e seu respectivo pagamento.
        </p>
        <p>Abaixo est um resumo do que cada mdulo principal faz.</p>
      </div>

      <div className="ajuda-section">
        <h3>Incio</h3>
        <p>
          Esta  a sua tela principal. Ela serve como um resumo rpido das atividades mais importantes do dia:
        </p>
        <ul>
          <li><b>Consultas do Dia:</b> Lista os agendamentos marcados para hoje.</li>
          <li><b>Pagamentos do Ms:</b> Mostra um resumo financeiro das parcelas que vencem no ms atual (previsto, pendente, vencido).</li>
          <li><b>Notificaes:</b> Exibe os 4 alertas mais recentes sobre pendncias (consultas, parcelas, oramentos antigos).</li>
          <li><b>Acessos Rpidos:</b> Atalhos para as aes mais comuns, como cadastrar um novo paciente ou oramento.</li>
        </ul>
      </div>

      <div className="ajuda-section">
        <h3>Pacientes (Cadastro e Histrico)</h3>
        <p>
          Este mdulo  o corao do sistema, onde voc gerencia as informaes dos seus pacientes.
        </p>
        <ol>
          <li><b>Listar Pacientes:</b> A tela principal lista todos os pacientes. Voc pode buscar por nome/CPF e editar ou excluir um cadastro.</li>
          <li><b>Novo Paciente:</b> O boto "Novo Paciente" inicia um formulrio de 4 etapas para cadastrar dados pessoais e a ficha de anamnese.</li>
          <li><b>Detalhes do Paciente:</b> Clicar no nome de um paciente leva  tela de detalhes, onde voc pode ver:
            <ul>
              <li>Os dados cadastrais completos.</li>
              <li>A ficha de anamnese preenchida.</li>
              <li>O histrico de todos os Oramentos e Tratamentos daquele paciente.</li>
            </ul>
          </li>
          <li><b>Fluxo de Tratamento:</b>  na tela de Detalhes do Paciente que voc "Aprova" um oramento, "Inicia" um tratamento e "Encerra" um tratamento.</li>
        </ol>
      </div>
      
      <div className="ajuda-section">
        <h3>Agenda</h3>
        <p>
          O mdulo de Agenda  a sua ferramenta central para gerenciar todos os horrios.
        </p>
        <ul>
          <li>
            <strong>Vises (Ms, Semana, Dia):</strong> Voc pode alternar entre as visualizaes para ter uma viso macro (ms) ou micro (dia/semana) dos seus horrios.
          </li>
          <li>
            <strong>Agendar:</strong> Clique em um horrio vago (na viso de Semana ou Dia) ou em um dia livre (na viso de Ms) para abrir o formulrio de "Nova Consulta".
          </li>
          <li>
            <strong>Bloqueios:</strong> O sistema automaticamente impede agendamentos em dias que j passaram e em Sbados ou Domingos (marcados como "Fechado").
          </li>
          <li>
            <strong>Paciente No Cadastrado:</strong> Ao criar uma nova consulta, voc pode usar o link "(Paciente no cadastrado)" para agendar rapidamente digitando apenas o nome, sem precisar fazer o cadastro completo.
          </li>
          <li>
            <strong>Detalhes da Consulta:</strong> Clicar em um agendamento j existente permite alterar o status (ex: de "Pendente" para "Confirmado"), reagendar ou excluir a consulta.
          </li>
        </ul>
      </div>
      
      <div className="ajuda-section">
        <h3>Oramentos</h3>
        <p>
          Esta tela  focada na <b>criao</b> de um novo oramento. O fluxo :
        </p>
        <ol>
          <li>Selecionar o Paciente e a Data.</li>
          <li>Selecionar o Procedimento (ex: Restaurao).</li>
          <li>Clicar no Odontograma (dente e faces), se o procedimento exigir.</li>
          <li>Adicionar o procedimento  lista.</li>
          <li>Definir o nmero de parcelas (opcional).</li>
          <li>Salvar o oramento, que aparecer no Histrico do paciente.</li>
        </ol>
      </div>
      
      <div className="ajuda-section">
        <h3>Pagamentos</h3>
        <p>
          Esta tela  o seu controle financeiro.
        </p>
        <ul>
          <li><b>Viso Geral (Padro):</b> Ao abrir a tela, ela j mostra uma lista de todas as parcelas "Pendentes" e "Atrasadas" de <b>todos</b> os pacientes, permitindo uma viso rpida de quem est devendo.</li>
          <li><b>Filtrar por Paciente:</b> Voc pode usar o seletor (opcional) para focar em um nico paciente. Isso habilitar a aba "Histrico (Pagos)".</li>
          <li><b>Aba Histrico (Pagos):</b> Mostra todas as parcelas que j foram quitadas <b>apenas</b> pelo paciente que voc selecionou no filtro.</li>
        </ul>
      </div>

      <div className="ajuda-section">
        <h3>Notificaes</h3>
        <p>
          Esta tela  sua caixa de entrada de alertas. O sistema gera notificaes automaticamente para:
        </p>
        <ul>
          <li>Consultas pendentes (no dia e na vspera).</li>
          <li>Parcelas pendentes (no dia e na vspera).</li>
          <li>Parcelas j atrasadas (resumo dirio).</li>
          <li>Oramentos aguardando aprovao h mais de 7 dias.</li>
        </ul>
        <p>Clicar em "OK" remove a notificao da lista.</p>
      </div>


      {/*Boto Flutuante de Ajuda*/}
      <button 
        className="floating-help-button" 
        onClick={() => setIsAjudaOpen(true)}
        title="Ajuda sobre esta pgina"
      >
        <FontAwesomeIcon icon={faQuestionCircle} />
      </button>

      {/*Modal de Ajuda*/}
      <Modal
        isOpen={isAjudaOpen}
        onClose={() => setIsAjudaOpen(false)}
        titulo="Ajuda - Ajuda Geral"
        footer={
          <button 
            className="modal-button modal-button-secondary" 
            onClick={() => setIsAjudaOpen(false)}
            title="Fechar esta janela."
          >
            Fechar
          </button>
        }
      >
        <p>Esta  a pgina de Ajuda Geral. Ela contm um resumo de todas as funcionalidades principais do sistema.</p>
      </Modal>

    </div>
  );
}

export default AjudaGeral;